<template>
  <div class="Registration">
    <div class="wrapper">
      <div class="title">Регистрация</div>

      <el-form
        ref="form"
        :model="user"
      >
        <el-form-item>
          <el-input
            v-model="user.first_name"
            placeholder="Имя"
          />
        </el-form-item>

        <el-form-item>
          <el-input
            v-model="user.last_name"
            placeholder="Фамилия"
          />
        </el-form-item>

        <el-form-item>
          <el-input
            v-model="user.second_name"
            placeholder="Отчество"
          />
        </el-form-item>

        <el-form-item>
          <el-input
            v-model="user.email"
            placeholder="Email"
          />
        </el-form-item>

        <el-form-item>
          <el-input
            v-model="user.password"
            type="password"
            placeholder="Пароль"
          />
        </el-form-item>

        <el-form-item>
          <el-input
            v-model="user.password_confirmation"
            type="password"
            placeholder="Подтверждение"
          />
        </el-form-item>

        <el-button
          type="primary"
          @click="register"
        >
          Зарегистрироваться
        </el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Registration',

  metaInfo: {
    title: 'Регистрация',
  },

  data() {
    return {
      user: {
        first_name: '',
        last_name: '',
        second_name: '',
        email: '',
        password: '',
        password_confirmation: '',
      },
    };
  },

  methods: {
    async register() {
      try {
        const { data } = await this.$http.post('/sign_up', { user: this.user });
        await this.$store.dispatch('auth/registration', data);
        this.$router.push({ name: 'Home' });
      } catch (err) {
        console.dir(err);
      }
    },
  },
};
</script>

